// src/components/ReservasForm.tsx
"use client";

import { useState, useEffect, FormEvent, ChangeEvent } from "react";
import { useSession } from "next-auth/react";
import { useRouter } from "next/navigation";
import Calendar from "react-calendar";
import "react-calendar/dist/Calendar.css";
import { Container, Form, Button, Alert, Row, Col } from "react-bootstrap";

const servicios = [
  "Estimulaci√≥n en agua",
  "Estimulaci√≥n en piso",
  "Quiropr√°ctica",
  "Fisioterapia",
  "Masajes",
  "Cosmetolog√≠a",
  "Prevenci√≥n de lesiones",
  "Preparaci√≥n f√≠sica",
  "Nutrici√≥n",
  "Medicina en rehabilitaci√≥n",
];

const terapeutas = [
  "Jes√∫s Ram√≠rez",
  "Miguel Ram√≠rez",
  "Alitzel Pacheco",
  "Francia",
  "Gisela",
];

export default function ReservasForm() {
  const { data: session, status } = useSession();
  const router = useRouter();
  const [servicio, setServicio] = useState("");
  const [terapeuta, setTerapeuta] = useState("");
  const [fecha, setFecha] = useState<Date | null>(null);
  const [horario, setHorario] = useState<number | null>(null);
  const [error, setError] = useState<string|null>(null);

  useEffect(() => {
    if (status === "unauthenticated") router.replace("/login");
  }, [status, router]);

  // Deshabilita DOMingos y los domingos
  const tileDisabled = ({ date }: { date: Date }) =>
    date.getDay() === 0 || date.getDay() === 6 && date.getHours() >= 15;

  const handleCalendarChange = (value: Date) => {
    setFecha(value);
    setHorario(null);
  };

  const horasSemana = Array.from({ length: 10 }, (_, i) => 10 + i);    // 10‚Äì19
  const horasSabado = Array.from({ length: 6 }, (_, i) => 9 + i);      // 9‚Äì14

  const onSubmit = (e: FormEvent) => {
    e.preventDefault();
    if (!servicio || !terapeuta || !fecha || horario === null) {
      setError("Completa todos los campos");
      return;
    }
    // Aqu√≠ llamas tu API de reserva y luego stripe
    console.log({ servicio, terapeuta, fecha, horario });
  };

  if (status === "loading" || !session) {
    return <Container className="py-5">Cargando‚Ä¶</Container>;
  }

  return (
    <Container className="py-5" style={{ maxWidth: 600 }}>
      <h2 className="mb-4 text-center">Reservar cita</h2>
      {error && <Alert variant="danger">{error}</Alert>}
      <Form onSubmit={onSubmit}>
        <Form.Select value={servicio} onChange={(e)=>setServicio(e.target.value)} required className="mb-3">
          <option value="">Servicio</option>
          {servicios.map(s => <option key={s} value={s}>{s}</option>)}
        </Form.Select>

        <Form.Select value={terapeuta} onChange={(e)=>setTerapeuta(e.target.value)} required className="mb-3">
          <option value="">Terapeuta</option>
          {terapeutas.map(t => <option key={t} value={t}>{t}</option>)}
        </Form.Select>

        <Calendar
          value={fecha ?? new Date()}
          onChange={handleCalendarChange}
          minDate={new Date()}
          tileDisabled={tileDisabled}
          className="mb-3"
        />

        {fecha && (
          <Row className="mb-3">
            {(fecha.getDay() === 6 ? horasSabado : horasSemana).map(h => (
              <Col xs="4" md="3" className="mb-2" key={h}>
                <Button
                  variant={horario === h ? "primary" : "outline-primary"}
                  onClick={() => setHorario(h)}
                  className="w-100"
                  type="button"
                >
                  {h}:00
                </Button>
              </Col>
            ))}
          </Row>
        )}

        <Button type="submit" className="w-100 btn-orange">
          Confirmar y pagar
        </Button>
      </Form>
    </Container>
  );
}
