// src/components/dashboard/PackagesSection.jsx
"use client";

import { useSession } from 'next-auth/react';
import { useRouter } from 'next/navigation';
import { useEffect, useState } from 'react';
import { loadStripe } from '@stripe/stripe-js';
import PackageCard from '../PackageCard';

export default function PackagesSection() {
  const { data: session } = useSession({ required: true });
  const router = useRouter();
  const [userPackages, setUserPackages] = useState([]);
  const [loading, setLoading] = useState(true);

  // IDs de precios en Stripe
  const INSCRIPCION = 'prod_SGiYWQCV47YwN6';
  const PAQUETES = [
    { id: '1', priceId: 'prod_SGib4AyvVeSSUp', name: 'Paquete 1', sessions:4, price:1400 },
    { id: '2', priceId: 'prod_SGigi2UsjCmfsE', name: 'Paquete 2', sessions:8, price:2250 },
    { id: '3', priceId: 'prod_SGik1G3nu0VtUV', name: 'Paquete 3', sessions:12, price:2500 },
  ];

  useEffect(() => {
    fetch('/api/user/packages')
      .then(r => r.json())
      .then(data => {
        setUserPackages(data);
        setLoading(false);
      });
  }, []);

  const buy = async (priceId) => {
    const r = await fetch('/api/stripe/checkout', {
      method:'POST',
      headers:{ 'Content-Type':'application/json' },
      body: JSON.stringify({ priceId }),
    });
    const { id } = await r.json();
    const stripe = await loadStripe(process.env.NEXT_PUBLIC_STRIPE_PUBLISHABLE_KEY);
    stripe.redirectToCheckout({ sessionId: id });
  };

  if (loading) return <p>Cargando paquetes‚Ä¶</p>;

  // Si no tiene inscripci√≥n: mostrar solo tarjeta de inscripci√≥n
  const hasInscripcion = userPackages.some(up => up.pkg.inscription === 1000);
  if (!hasInscripcion) {
    return (
      <>
        <h3 className="text-center mb-4">Inscripci√≥n</h3>
        <div className="row justify-content-center">
          <div className="col-md-4">
            <PackageCard
              pkg={{ name: 'Inscripci√≥n Bloom', price: 1000, sessions: 0 }}
              onBuy={() => buy(INSCRIPCION)}
              inscriptionOnly
            />
          </div>
        </div>
      </>
    );
  }

  // Si ya pag√≥ inscripci√≥n, mostrar paquetes
  return (
    <>
      <h3 className="text-center mb-4">Paquetes de Estimulaci√≥n en agua</h3>
      <div className="row g-4">
        {PAQUETES.map(p => (
          <div className="col-md-4" key={p.id}>
            <PackageCard
              pkg={p}
              onBuy={() => buy(p.priceId)}
            />
          </div>
        ))}
      </div>
    </>
  );
}
